<template>
<div class='groups'>
  <b-modal id='modal1' ref="myModalRef" title="系統訊息" @ok="deleteItem">
    <p class="my-4">確認是否刪除這個飲料團?</p>
  </b-modal>
  <table class="table">
    <thead>
      <th>
      </th>
      <th>
      </th>
      <th><button type="button" class="btn btn-primary" v-on:click='create'><i class="fas fa-plus-circle"></i></button></th>
      <th>開放時間</th>
      <th>總人數</th>
      <th>總件數</th>
      <th>總金額</th>
      <th>團主</th>
    </thead>
    <tbody>
      <tr v-for='item in groups'>
        <td>
          <div v-show='$user.uid == item.createdBy.uid'>
            <b-button variant="danger" v-on:click='confirmDeleteItem(item)' v-show='$user.uid == item.createdBy.uid'> <i class="fas fa-trash-alt"></i>
            </b-button>
            <b-form-radio-group button-variant="outline-primary" buttons v-model="item.enabled" :options="[{text:'啟用',value:'Y'},{text:'關閉',value:'N'}]" name="radiosBtnDefault" @change='updateItem(item, "enabled", $event)' />
          </div>
        </td>
        <td>
          <b-link :to="{ name: 'Orders', params: { groupKey: item['.key'] }}" v-b-tooltip.hover title="訂一杯吧!">
            <i class="fas fa-beer"></i>
          </b-link>
        </td>

        <td>
          <span v-show='!(($user.uid == item.createdBy.uid) && (item.enabled == "N"))'>{{item.name}}</span>
          <input class="form-control" :value="item.name" v-show='($user.uid == item.createdBy.uid) && (item.enabled == "N")' @change="updateItem(item, 'name', $event.target.value)">
        </td>
        <td>
          <span v-show='!(($user.uid == item.createdBy.uid) && (item.enabled == "N"))'>{{ formatDateTime(item.startAt) }} - {{ formatDateTime(item.finishAt) }}</span>
          <div v-show='($user.uid == item.createdBy.uid) && (item.enabled == "N")'>
            <b-badge>
              開始時間</b-badge>
            <input class="form-control" type='datetime-local' :value='item.startAt' @change="updateItem(item, 'startAt', $event.target.value)">
            <b-badge>
              結束時間</b-badge>
            <input class="form-control" type='datetime-local' :value='item.finishAt' @change="updateItem(item, 'finishAt', $event.target.value)">
          </div>
        </td>
        <td>{{item.totalPeople}}</td>
        <td>{{item.totalItems}}</td>
        <td>{{item.totalAmount}}</td>
        <td>
          <b-img rounded width='32' v-bind:src="item.createdBy.photoURL" />
          <b-badge>{{item.createdBy.displayName}}</b-badge>
        </td>
      </tr>
    </tbody>
  </table>
</div>
</template>

<script>
import moment from 'moment'
import firebase from 'firebase'
import {
  firebaseDb
} from '../firebaseDb'
// var storageRef = firebase.storage().ref();

export default {
  name: 'Groups',
  firebase: {
    groups: {
      source: firebaseDb.ref('/vue-app006/groups/')
    }
    // anArray: firebase.database().ref('/groups/')
  },
  data() {
    return {
      loading: true,
      deleteItemKey: '',
      test: moment().format('YYYY-MM-DDTHH:mm:ss')
    }
  },
  computed: {

  },
  methods: {
    create() {
      this.$firebaseRefs.groups.push({
        name: '[未命名的飲料團]',
        enabled: "N",
        startAt: moment().add(1, 'days').format('YYYY-MM-DDTHH:mm:ss'),
        finishAt: moment().add(3, 'days').format('YYYY-MM-DDTHH:mm:ss'),
        totalPeople: 0,
        totalItems: 0,
        totalAmount: 0,
        createdBy: this.$user,
        createdAt: moment().format('YYYY-MM-DDTHH:mm:ss')
      })
    },
    confirmDeleteItem: function(item) {
      // 還沒找到如何在show的加入callback,就不必把key另存起來
      this.$refs.myModalRef.show()
      this.deleteItemKey = item['.key']
    },
    deleteItem: function() {
      this.$firebaseRefs.groups.child(this.deleteItemKey).remove()
    },
    updateItem: function(item, field, newValue) {
      this.$firebaseRefs.groups.child(item['.key']).child(field).set(newValue)
    },
    formatDateTime: function(value) {
      return moment(value).format("MM/DD HH:mm")
    },
    buttonGroupChage: function(event) {
      console.log(event)

    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



