<template>
<div class='orders'>
  <b-modal ref="myConfirmModalRef" title="系統訊息" @ok="deleteItem">
    <p class="my-4">確認是否刪除這個項目?</p>
  </b-modal>

  <b-modal ref="mySelectModalRef" title="挑選商品" @ok="onClickOk">
    <form>
      <div class="form-group">
        <select class="form-control" @change="refreshItems($event.target.value)" v-if='selectorShops'>
          <option v-for='shop in selectorShops' v-bind:value='shop[".key"]'>
            {{ shop.name }}
          </option>
        </select>
      </div>
      <div class="form-group">
        <select class="form-control" v-if='selectorItems' ref='selectorItem'>
          <option v-for='item in selectorItems' v-bind:value='item[".key"]'>
            {{ item.name }} - {{ item.size }}
          </option>
        </select>
      </div>
    </form>
  </b-modal>


  <h3>訂購項目 <span class="badge badge-secondary">{{ group.name }}</span></h3>
  <table class="table">
    <thead>
      <th><button type="button" class="btn btn-primary" v-on:click='create'><i class="fas fa-plus-square"></i></button></th>
      <th>品名</th>
      <th>規格</th>
      <th style='width:100px'>價格</th>
      <th>備註</th>
      <th></th>
    </thead>
    <tbody>
      <tr v-for='item in sortedItems'>
        <td>
          <b-button variant="success" v-on:click='copy(item)'> <i class="fas fa-copy"></i></i>
          </b-button>
          <b-button v-show='$user.uid == item.createdBy.uid' variant="danger" v-on:click='confirmDeleteItem(item)'> <i class="fas fa-trash-alt"></i>
          </b-button>
        </td>
        <td>
          <b-input-group v-show='$user.uid == item.createdBy.uid'>
            <b-input-group-text slot="append">
              <b-button variant="info" :size='"sm"' v-on:click='selectShopItem(item)'>
                <i class="fas fa-search"></i>
              </b-button>
            </b-input-group-text>
            <input v-show='$user.uid == item.createdBy.uid' class="form-control" :value="item.name" @change="updateItem(item, 'name', $event.target.value)">
          </b-input-group>

          <span v-show='$user.uid != item.createdBy.uid'> {{ item.name }} </span>
        </td>
        <td>
          <select v-show='$user.uid == item.createdBy.uid' class="form-control" :value="item.size" @change="updateItem(item, 'size', $event.target.value)">
            <option>---</option>
            <option>小</option>
            <option>中</option>
            <option>大</option>
            <option>瓶</option>
          </select>
          <span v-show='$user.uid != item.createdBy.uid'> {{ item.size }} </span>
        </td>
        <td>
          <input v-show='$user.uid == item.createdBy.uid' type="number" class="form-control" :value="item.price" @change="updateItem(item, 'price', $event.target.value)">
          <span v-show='$user.uid != item.createdBy.uid'> {{ item.price }} </span>
        </td>
        <td>
          <input v-show='$user.uid == item.createdBy.uid' type="text" class="form-control" :value="item.note" @change="updateItem(item, 'note', $event.target.value)">
          <span v-show='$user.uid != item.createdBy.uid'> {{ item.note }} </span>
        </td>
        <td>
          <b-img rounded width='32' v-bind:src="item.createdBy.photoURL" />
          <b-badge>{{item.createdBy.displayName}}</b-badge>
        </td>
      </tr>
    </tbody>
  </table>
</div>
</template>

<script>
import moment from 'moment'
import firebase from 'firebase'
import {
  firebaseDb
} from '../firebaseDb'
// var storageRef = firebase.storage().ref();

export default {
  name: 'Orders',
  firebase: {

    // items: { source: firebaseDb.ref('/vue-app006/items/') }
    // shops: { source: firebaseDb.ref('/vue-app006/shops/') }
  },
  data() {
    return {
      loading: true,
      deleteItemKey: '',
      selectedOrderKey: '',
    }
  },
  computed: {
    sortedItems() {
      return this.items.sort((a, b) => a.name > b.name);
    }
  },
  created() {
    //this.$bindAsObject('shop', this.$firebaseRefs.shops.child(this.$route.params.shopKey))
    this.$bindAsObject('group', firebaseDb.ref('/vue-app006/groups/' + this.$route.params.groupKey))
    this.$bindAsArray('items', firebaseDb.ref('/vue-app006/orders/').orderByChild("groupKey").equalTo(this.$route.params.groupKey));

    // 挑選視窗裡的連動下拉選單
    var vm = this;
    this.$bindAsArray('selectorShops', firebaseDb.ref('/vue-app006/shops/'), null, () => {
      vm.refreshItems(this.selectorShops[0]['.key']);
    });


  },
  watch: {

  },
  methods: {
    onClickOk(evt) {
      console.log(evt);
      console.log(this.$refs.selectorItem.value);
      var vm = this;
      firebaseDb.ref('/vue-app006/items/' + this.$refs.selectorItem.value).once('value', function(data) {
        vm.$firebaseRefs.items.child(vm.selectedOrderKey).child("name").set(data.val().name)
        vm.$firebaseRefs.items.child(vm.selectedOrderKey).child("price").set(data.val().price)
        vm.$firebaseRefs.items.child(vm.selectedOrderKey).child("size").set(data.val().size)
      })

    },
    refreshItems(shopKey) {
      this.$bindAsArray('selectorItems', firebaseDb.ref('/vue-app006/items/').orderByChild("shopKey").equalTo(shopKey));
    },
    create() {
      this.$firebaseRefs.items.push({
        groupKey: this.group['.key'],
        name: '[未命名的商品]',
        size: '---',
        price: 50,
        note: '',
        createdBy: this.$user,
        createdAt: moment().format('YYYY-MM-DDTHH:mm:ss')
      })
      this.$firebaseRefs.group.child("totalItems").set(this.items.length)
    },
    copy: function(item) {
      this.$firebaseRefs.items.push({
        groupKey: this.group['.key'],
        name: item.name,
        size: item.size,
        price: item.price,
        note: item.note,
        createdBy: this.$user,
        createdAt: moment().format('YYYY-MM-DDTHH:mm:ss')
      })
    },
    selectShopItem: function(item) {
      this.selectedOrderKey = item['.key'];
      console.log(this.selectedOrderKey);
      this.$refs.mySelectModalRef.show();
    },
    confirmDeleteItem: function(item) {
      // 還沒找到如何在show的加入callback,就不必把key另存起來
      this.$refs.myConfirmModalRef.show()
      this.deleteItemKey = item['.key']
    },
    deleteItem: function() {
      this.$firebaseRefs.items.child(this.deleteItemKey).remove()
      this.$firebaseRefs.group.child("totalItems").set(this.items.length)
    },
    updateItem: function(item, field, newValue) {
      this.$firebaseRefs.items.child(item['.key']).child(field).set(newValue)
    },
    formatDateTime: function(value) {
      return moment(value).format("MM/DD HH:mm")
    },
    buttonGroupChage: function(event) {
      console.log(event)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



