<template>
<div class='items'>
  <b-modal id='modal1' ref="myModalRef" title="系統訊息" @ok="deleteItem">
    <p class="my-4">確認是否刪除這個項目?</p>
  </b-modal>

  <h3>商品設定 <span class="badge badge-secondary">{{ shop.name }}</span></h3>
  <table class="table">
    <thead>
      <th><button type="button" class="btn btn-primary" v-on:click='create'><i class="fas fa-plus-square"></i></button></th>
      <th>品名</th>
      <th>規格</th>
      <th style='width:100px'>價格</th>
    </thead>
    <tbody>
      <tr v-for='item in sortedItems'>
        <td>
          <div>
            <b-button variant="danger" v-on:click='confirmDeleteItem(item)'> <i class="fas fa-trash-alt"></i>
            </b-button>
            <b-button variant="success" v-on:click='copy(item)'> <i class="fas fa-copy"></i></i>
            </b-button>
          </div>
        </td>
        <td>
          <input class="form-control" :value="item.name" @change="updateItem(item, 'name', $event.target.value)">
        </td>
        <td>
          <select class="form-control" :value="item.size" @change="updateItem(item, 'size', $event.target.value)">
            <option>---</option>
            <option>小</option>
            <option>中</option>
            <option>大</option>
            <option>瓶</option>
          </select>
        </td>
        <td>

          <input type="number" class="form-control" :value="item.price" @change="updateItem(item, 'price', $event.target.value)">
        </td>
      </tr>
    </tbody>
  </table>
</div>
</template>

<script>
import moment from 'moment'
import firebase from 'firebase'
import {
  firebaseDb
} from '../firebaseDb'
// var storageRef = firebase.storage().ref();

export default {
  name: 'Items',
  firebase: {
    //items: { source: firebaseDb.ref('/vue-app006/items/') }
    shops: {
      source: firebaseDb.ref('/vue-app006/shops/')
    }
  },
  data() {
    return {
      loading: true,
      deleteItemKey: ''
    }
  },
  computed: {
    sortedItems() {
      return this.items.sort((a, b) => a.name > b.name);
    }
  },
  created() {
    //this.$bindAsObject('shop', this.$firebaseRefs.shops.child(this.$route.params.shopKey))
    this.$bindAsObject('shop', firebaseDb.ref('/vue-app006/shops/' + this.$route.params.shopKey))
    this.$bindAsArray('items', firebaseDb.ref('/vue-app006/items/').orderByChild("shopKey").equalTo(this.$route.params.shopKey));
  },
  methods: {
    create() {
      this.$firebaseRefs.items.push({
        shopKey: this.shop['.key'],
        name: '[未命名的商品]',
        size: "---",
        price: 50
      })
      this.$firebaseRefs.shop.child("totalItems").set(this.items.length)
    },
    copy: function(item) {
      this.$firebaseRefs.items.push({
        shopKey: this.shop['.key'],
        name: item.name,
        size: item.size,
        price: item.price
      })
    },
    confirmDeleteItem: function(item) {
      // 還沒找到如何在show的加入callback,就不必把key另存起來
      this.$refs.myModalRef.show()
      this.deleteItemKey = item['.key']
    },
    deleteItem: function() {
      this.$firebaseRefs.items.child(this.deleteItemKey).remove()
      this.$firebaseRefs.shop.child("totalItems").set(this.items.length)
    },
    updateItem: function(item, field, newValue) {
      this.$firebaseRefs.items.child(item['.key']).child(field).set(newValue)
    },
    formatDateTime: function(value) {
      return moment(value).format("MM/DD HH:mm")
    },
    buttonGroupChage: function(event) {
      console.log(event)

    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



