import Vue from 'vue'
import Router from 'vue-router'

import Login from '@/components/Login'
import Groups from '@/components/Groups'
import Orders from '@/components/Orders'
import Shops from '@/components/Shops'
import Items from '@/components/Items'

import firebase from 'firebase'

Vue.use(Router)

let router = new Router({
  routes: [{
    path: '*',
    redirect: '/login'
  }, {
    path: '/',
    redirect: '/login'
  }, {
    path: '/login',
    name: 'Login',
    component: Login
  }, {
    path: '/groups',
    name: 'Groups',
    component: Groups,
    meta: {
      requiresAuth: true
    }
  }, {
    path: '/orders/:groupKey',
    name: 'Orders',
    component: Orders,
    meta: {
      requiresAuth: true
    }
  }, {
    path: '/shops',
    name: 'Shops',
    component: Shops,
    meta: {
      requiresAuth: true
    }
  }, {
    path: '/items/:shopKey',
    name: 'Items',
    component: Items,
    meta: {
      requiresAuth: true
    }
  }]
})

router.beforeEach((to, from, next) => {
  // 這裡會一直檢查登入情形
  let currentUser = firebase.auth().currentUser
  let requiresAuth = to.matched.some(record => record.meta.requiresAuth)

  if (currentUser) {
    Vue.prototype.$user.displayName = currentUser.displayName
    Vue.prototype.$user.photoURL = currentUser.photoURL
    Vue.prototype.$user.email = currentUser.email
    Vue.prototype.$user.uid = currentUser.uid
  }

  if (requiresAuth && !currentUser) next('login')
  else if (!requiresAuth && currentUser) next('groups')
  else next()
})

export default router



