<template>
<div class='shops'>
  <b-modal ref="myConfirmModalRef" title="系統訊息" @ok="deleteItem">
    <p class="my-4">確認是否刪除這個飲料團?</p>
  </b-modal>
  <b-modal ref="myAlertModalRef" ok-only title="系統訊息">
    <p class="my-4">請先刪除店家裡設定的所有項目,才能刪除店家資料.</p>
  </b-modal>
  <table class="table">
    <thead>
      <th>
        <b-button variant="primary" v-on:click='create'> <i class="fas fa-plus-circle"></i>
        </b-button>
      </th>
      <th></th>
      <th>店家名稱</th>
      <th>類型</th>
      <th>地址</th>
      <th>電話</th>
      <th>位置</th>
      <th>項目</th>
    </thead>
    <tbody>
      <tr v-for='item in shops'>
        <td>
          <b-button variant="danger" v-on:click='confirmDeleteItem(item)'> <i class="fas fa-trash-alt"></i>
          </b-button>

          <b-link :to="{ name: 'Items', params: { shopKey: item['.key'] }}"> <i class="fas fa-list"></i></b-link>
        </td>
        <td>
          <b-form-radio-group id="btnradios1" button-variant="outline-primary" buttons v-model="item.enabled" :options="[{text:'啟用',value:'Y'},{text:'關閉',value:'N'}]" name="radiosBtnDefault" @change='updateItem(item, "enabled", $event)' />
          </td<>
          <td>
            <input class="form-control" :value="item.name" @change="updateItem(item, 'name', $event.target.value)">
          </td>
          <td>
            <select class="form-control" :value="item.genre" @change="updateItem(item, 'genre', $event.target.value)">
              <option>飲料</option>
              <option>中餐</option>
              <option>炸物</option>
            </select>
          </td>
          <td>{{item.address}}</td>
          <td>{{item.telephone}}</td>
          <td>
            <select class="form-control" :value="item.location" @change="updateItem(item, 'location', $event.target.value)">
              <option>---</option>
              <option>廠區</option>
              <option>總部</option>
            </select>
          </td>
          <td>
            <b-badge variant='success' v-if='item.totalItems>0'> {{item.totalItems}}</b-badge>
            <b-badge variant='danger' v-if='item.totalItems==0'> 尚未設定 </b-badge>
          </td>
      </tr>
    </tbody>
  </table>
</div>
</template>

<script>
import moment from 'moment'
import firebase from 'firebase'
import {
  firebaseDb
} from '../firebaseDb'
// var storageRef = firebase.storage().ref();

export default {
  name: 'Shops',
  firebase: {
    shops: {
      source: firebaseDb.ref('/vue-app006/shops/')
    }
    // anArray: firebase.database().ref('/groups/')
  },
  data() {
    return {
      loading: true,
      deleteItemKey: ''
    }
  },
  computed: {

  },
  methods: {
    create() {
      this.$firebaseRefs.shops.push({
        name: '[未命名的店家]',
        enabled: 'N',
        genre: '飲料',
        address: '',
        telephone: '',
        location: '總部',
        totalItems: 0
      })
    },
    mounted() {
      console.log(this.params.shopKey)
    },
    confirmDeleteItem(item) {
      var vm = this
      firebaseDb.ref('/vue-app006/items/').orderByChild("shopKey").equalTo(item['.key']).once('value').then(
        function(snapshot) {
          vm.deleteItemKey = ''
          if (snapshot.numChildren() === 0) {
            vm.deleteItemKey = item['.key']
            vm.$refs.myConfirmModalRef.show()
          } else {
            vm.$refs.myAlertModalRef.show()
          }
        }
      )
    },
    deleteItem() {
      this.$firebaseRefs.shops.child(this.deleteItemKey).remove()
    },
    updateItem(item, field, newValue) {
      this.$firebaseRefs.shops.child(item['.key']).child(field).set(newValue)
    },
    formatDateTime(value) {
      return moment(value).format('MM/DD HH:mm')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



